.AnswerItem {
  border: 1px solid #ffffff;
  padding: 5px 10px;
  border-radius: 5px;
  margin-top: 10px;
  cursor: pointer
}

.AnswerItem:hover {
  background: rgba(255, 255, 255, .2);
  transition: background .3s ease-in-out;
}

.AnswerItem.success {
  background-color: rgba(161, 240, 69, .7);
}

.AnswerItem.error {
  background-color: rgba(240, 87, 108, .7);
}